*{
    box-sizing: border-box;
}


body {
    margin:0;
    padding:0;
    font-family: -apple-system, BlinkMacSystemFont, 
    'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
     'Open Sans', 'Helvetica Neue', sans-serif;
    background-color: #2f2f2f;
    height: 100%;
    
}

html, body, #root .container{
    height: 100%;
    display: flex;
    justify-content: center;
}

.loader{
    width:100%;
    height:100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.movies{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 50px;
    width: 80%;
}

.movies .movies_movie{
    width: 45%;
    background-color: white;
    margin-bottom: 70px;
    display: flex;
    
    align-items: flex-start;
    font-weight: 300;
}

.movies_movie img{
    float: left;
    max-width: 120px;
    position: relative;
    top:-50px;
    
}

.movies_movie h3, .movies_movie h5{
    font-weight: 300;
    margin: 0px;
}